.top {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 0%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #000000;
  padding: 15px 20px;
}
.weathericon{
  display: block;
  margin: auto;
  height: 120px;
  padding: 15px 20px;
}
.vertical-line {
  width: 109px;
  height: 0px;
  border: 1px solid #000000;
  transform: rotate(-90deg);
  margin: 24px auto 24px;
}
.weather-details {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  align-items: center;
  font-weight: 700;
  font-size: 16px;
  text-align: center;
  padding: 15px 20px;
}
