.forecast {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: auto;
  padding: 10px 20px;
}
.forecast-button {
  border: 2px solid black;
  border-radius: 100px;
  width: 160px;
  margin: 3px;
  align-items: center;
  cursor: pointer;
  display: flex;
  justify-content: center;
  font-weight: 600;
  font-size: 16px;
  text-align: center;
  padding: 5px 20px;
}
.active,
.forecast-button:hover {
  background-color: #F4F9FC;
}
.forecast-details {
  grid-row-gap: 0;
  grid-column-gap: 15px;
  -webkit-column-gap: 15px;
  column-gap: 20px;
  display: grid;
  grid-template-columns: auto auto;
  justify-content: center;
  align-items: center;
}
.min-max {
  font-weight: 600;
  font-size: 16px;
  align-items: center;
  display: flex;
  height: 20px;
  gap: 10px;
  justify-content: space-between;
}
